import React from 'react';
import { Card, CardMedia, CardContent, Typography } from '@mui/material';

const Item = ({ imageSrc, title, description }) => {
  return (
    <Card>
      <CardMedia component="img" src={imageSrc} alt="Item" height="300" />
      <CardContent>
        <Typography variant="h6" sx={{ fontSize: '14px', mb: 1 }}>{title}</Typography>
        <Typography variant="body2" sx={{ fontSize: '12px' }}>{description}</Typography>
      </CardContent>
    </Card>
  );
};

export default Item;
