import { useEffect } from "react";
import{ BrowserRouter, Routes, Route, useLocation } from 'react-router-dom';

import ItemDetails from "./scenes/itemDetails/ItemDetails";
import Checkout from "./scenes/checkout/Checkout";
import Confirmation from "./scenes/checkout/Confirmation";
import Navbar from "./scenes/global/Navbar";
import CartMenu from "./scenes/global/CartMenu";
import Footer from "./scenes/global/Footer";
import {Helmet} from "react-helmet";
import Home from './scenes/home/Home';



const ScrollToTop = () =>{
  const { pathname } = useLocation ();

  useEffect (() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
};

function App() {
  return (
    <div className="app">
       <Helmet>
                <meta charSet="utf-8" />
                <title>Assignment Helpers</title>
                <meta name="description" content="Get professional assignment help and solutions for your homework and academic tasks. At assignment helpers, we provide expert assistance to ensure your success in all subjects and academic work. Contact us now!"/>

            </Helmet>
      <BrowserRouter>
      <Navbar />
      <ScrollToTop />
       
      <Routes>
        
       <Route path="/" element ={<Home />} /> 
        <Route path="item/:itemId" element ={<ItemDetails/>} />
        <Route path="checkout" element ={<Checkout />} />
        <Route path="checkout/success" element ={<Confirmation />} />
      </Routes>
 
      <CartMenu/>
     
      <Footer/>
      </BrowserRouter>
      </div>
  );
}

export default App;
