import React from "react";
import { Box, Typography, Tab, Tabs, useMediaQuery, Button } from "@mui/material";
import Item from "../../components/Item";

import bulbTechnicalImage from '../../bodyImages/bulb technical.png';
import candleNonTechImage from '../../bodyImages/candle non tech.png';
import codingITImage from '../../bodyImages/coding IT.png';
import LiteratureImage from '../../bodyImages/Literature.png';
import MathsPhysicsImage from '../../bodyImages/Maths Physics.png';
import socialSciencesImage from '../../bodyImages/social sciences.png';
import conicalFlaskImage from '../../bodyImages/conical flask.png';
import bookNonTechImage from '../../bodyImages/book non tech.png'
import humanitiesBooksImage from '../../bodyImages/Humanities books.png'



const ShoppingList = () => {
  const [value, setValue] = React.useState("all");
  const isNonMobile = useMediaQuery("(min-width: 600px)");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleEmailClick = () => {
    const subject = encodeURIComponent("Assignment Details");
    const mailtoLink = document.createElement("a");
    mailtoLink.href = `mailto:assignhelpa@gmail.com?subject=${subject}`;
    mailtoLink.click();
  };

  const handlePaypalClick = () => {
    const paypalUrl = `https://www.paypal.com/login?returnUri=https://www.paypal.com/sendmoney`;
    window.open(paypalUrl, "_blank");
  };

  return (
    <Box width="80%" margin="80px auto">
      <Typography variant="h3" textAlign="center">
        Your Assignment <b>Categories</b>
      </Typography>
      <Tabs
  textColor="primary"
  indicatorColor="primary"
  value={value}
  onChange={handleChange}
  centered
  TabIndicatorProps={{ sx: { display: isNonMobile ? "block" : "none" } }}
  sx={{
    m: "25px",
    "& .MuiTabs-flexContainer": {
      flexWrap: "wrap",
    },
  }}
>
  <Tab label="ALL" value="all" />
  <Tab
    label="CERTIFICATE, HIGHSCHOOL & others"
    value="certificateHighschool"
  />
  <Tab label="MASTERS, & PHD" value="mastersPHD" />
  <Tab label="DEGREE, & DIPLOMA" value="degreeDiploma" />
</Tabs>


<Box
        margin="0 auto"
        display="grid"
        gridTemplateColumns="repeat(auto-fill, 300px)"
        justifyContent="space-around"
        rowGap="20px"
        columnGap="1.33%"
      >
        {value === 'all' && (
          <>
             <Item imageSrc={bookNonTechImage} title="Non Technicals" description="Highschool level [$7]"/>
            <Item imageSrc={bulbTechnicalImage} title="Technicals" description=" Highschool level [$9]" />
            <Item imageSrc={codingITImage} title="Coding " description="All levels [$13]" />
            <Item imageSrc={LiteratureImage} title="Literature" description="Degree & Diploma [$8]" />
            <Item imageSrc={conicalFlaskImage} title="Sciences" description="Degree & Diploma [$8]" />
            <Item imageSrc={humanitiesBooksImage} title="Humanities" description="Degree & Diploma [$8]" />
            <Item imageSrc={MathsPhysicsImage} title="Maths & Physics" description="Degree & Diploma [$10]" />
            <Item imageSrc={socialSciencesImage} title="Social Sciences" description="Masters, Degree & Diploma [$8]" />
            <Item imageSrc={candleNonTechImage} title="Non Technicals" description="Degree & Diploma [$8]"/>
           
          </>
        )}
        {value === 'certificateHighschool' && (
          <>
            <Item imageSrc={LiteratureImage} title="Literature" description=" Highschool level [$7]" />
            <Item imageSrc={MathsPhysicsImage} title="Maths Physics" description="Highschool level [$9]" />
            <Item imageSrc={candleNonTechImage} title="Non Technicals" description="Highschool level [$7]"/>
          </>
        )}
        {value === 'mastersPHD' && (
          <>
            <Item imageSrc={socialSciencesImage} title="Social Sciences" description="Masters & PHD [$9]" />
            <Item imageSrc={conicalFlaskImage} title="Technicals" description="Masters & PHD [$12]" />
            <Item imageSrc={candleNonTechImage} title="Non Technicals" description="Masters & PHD [$10]"/>
          </>
        )}
        {value === 'degreeDiploma' && (
          <>
            <Item imageSrc={LiteratureImage} title="Literature" description="Degree & Diploma [$8]" />
            <Item imageSrc={conicalFlaskImage} title="Sciences" description="Degree & Diploma [$8]" />
            <Item imageSrc={humanitiesBooksImage} title="Humanities" description="Degree & Diploma [$8]" />
            <Item imageSrc={MathsPhysicsImage} title="Maths & Physics" description="Degree & Diploma [$10]" />
            <Item imageSrc={socialSciencesImage} title="Social Sciences" description="Degree & Diploma [$8]" />
            <Item imageSrc={candleNonTechImage} title="Non Technicals" description="Degree & Diploma [$8]"/>
          </>
        )}
      </Box>


      <Typography textAlign="center" fontSize="15px" marginTop="40px">
        Make a Quick Upload Of Your Assignment And Fee Below
      </Typography>
      <Typography textAlign="center" marginTop="20px">
        <b>Address: denniswakaba700@gmail.com</b>
      </Typography>
      <Box
        display="flex"
        justifyContent="space-between"
        marginTop="40px"
        paddingBottom="20px"
      >
        <Button
          variant="contained"
          color="primary"
          size="large"
          onClick={handleEmailClick}
          sx={{
            animation: "flash 10s infinite",
            width: "300px",
            backgroundColor: "green",
            "&:hover": {
              backgroundColor: "green",
            },
          }}
        >
          QUICK UPLOAD TO TUTOR EMAIL
        </Button>
        <Button
          variant="contained"
          color="primary"
          size="large"
          onClick={handlePaypalClick}
          sx={{
            animation: "flash 10s infinite",
            width: "300px",
            backgroundColor: "blue",
            "&:hover": {
              backgroundColor: "blue",
            },
            marginLeft: "20px",
          }}
        >
          Quick PAY VIA PAYPAL
        </Button>
      </Box>
    </Box>
  );
};

export default ShoppingList;
