import { Box, InputBase, Divider, Typography, IconButton } from '@mui/material';
import MarkEmailReadOutlineIcon from '@mui/icons-material/MarkEmailReadOutlined';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { useState } from "react";

const Subscribe = () => {
    const [email, setEmail] = useState("");
    const [subscribed, setSubscribed] = useState(false);
    const [emailError, setEmailError] = useState(false);

    const handleSubscribe = () => {
        if (validateEmail(email)) {
            setSubscribed(true);
            setEmailError(false);
        } else {
            setEmailError(true);
        }
    };

    const validateEmail = (email) => {
        // Email validation logic
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        return emailRegex.test(email);
    };

    return (
        <Box width="80%" margin="80px auto" textAlign="center">
            <IconButton>
                <MarkEmailReadOutlineIcon fontSize="large" />
            </IconButton>
            <Typography variant="h3">Subscribe To Our Newsletter</Typography>
            <Typography> and receive <b>20%</b> coupon on your first order</Typography>
            <Box
                p="2px 4px"
                m="15px auto"
                display="flex"
                alignItems="center"
                width="75%"
                backgroundColor="#f2f2f2"
            >
                <InputBase
                    sx={{ ml: 1, flex: 1 }}
                    placeholder="Enter email"
                    onChange={(e) => setEmail(e.target.value)}
                    value={email}
                    error={emailError}
                />
                <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
                {subscribed ? (
                    <Typography sx={{ p: "10px", color: "green" }}>
                        Subscribed <CheckCircleIcon />
                    </Typography>
                ) : (
                    <Typography
                        sx={{
                            p: "10px",
                            ":hover": { cursor: "pointer" },
                            color: emailError ? "red" : "inherit",
                        }}
                        onClick={handleSubscribe}
                    >
                        {emailError ? "Invalid Email" : "Subscribe"}
                    </Typography>
                )}
            </Box>
        </Box>
    );
};

export default Subscribe;
