import { useState, useEffect } from 'react';
import { Badge, Box, IconButton, Snackbar, Typography, TextField } from "@mui/material";
import {
    SearchOutlined,
    ShoppingBagOutlined,
    MenuOutlined,
    NotificationsOutlined,
    CloseOutlined,
    Fingerprint,
    WhatsApp as WhatsAppIcon
} from "@mui/icons-material";
import { useNavigate } from "react-router-dom";
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import { TurnedIn, Spellcheck, GTranslate, MenuBook, School, LibraryBooks } from "@mui/icons-material";
import { Tooltip } from '@mui/material';
import EmailIcon from '@mui/icons-material/Email';





const Navbar = () => {
    const navigate = useNavigate();
    const [showHomeText, setShowHomeText] = useState(false);
    const [showNotification, setShowNotification] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');
    const [searchNotFound, setSearchNotFound] = useState(false);
    const [isShaking, setIsShaking] = useState(false);
    const [showShoppingBagPanel, setShowShoppingBagPanel] = useState(false); // Added state

    useEffect(() => {
        const interval = setInterval(() => {
            setIsShaking(prevState => !prevState);
        }, 3000);

        return () => {
            clearInterval(interval);
        };
    }, []);

    const handleMenuClick = () => {
        setShowHomeText(!showHomeText);
    };
    const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

    const handleNotificationClick = () => {
        setShowNotification(true);
    };

    const handleNotificationClose = () => {
        setShowNotification(false);
    };

    const handleHomeClick = () => {
        navigate("/");
    };
    const handleEmailClick = () => {
        window.location.href = "mailto:assignhelpa@gmail.com?subject=Assignment%20Help";
      };

    const handleSearch = () => {
        if (searchQuery) {
            setSearchNotFound(true);
        }
    };

    const handleSearchInputChange = (event) => {
        setSearchQuery(event.target.value);
    };

    const handleWhatsAppClick = () => {
        window.open("https://wa.me/+254701967780", "_blank");
    };

    const handleShoppingBagClick = () => {
        setShowShoppingBagPanel(true);
    };

    const handleClosePanel = () => {
        setShowShoppingBagPanel(false);
    };

    return (
        <Box
            display="flex"
            alignItems="center"
            width="100%"
            height="60px"
            backgroundColor="rgba(255, 255, 255, 0.95)"
            color="black"
            position="fixed"
            top="0"
            left="0"
            zIndex="1"
        >
            <Box
                width="80%"
                margin="auto"
                display="flex"
                justifyContent="space-between"
                alignItems="center"
            >
                <Box
                    display="flex"
                    alignItems="center"
                    onClick={() => navigate("/")}
                    sx={{ '&:hover': { cursor: "pointer" } }}
                    color="#FF0000" // Bright red color
                >
                    <Fingerprint sx={{ fontSize: 30, marginRight: 5 }} />
                    <b>Assignment Helpers</b>
                </Box>

                <Box
                    display="flex"
                    alignItems="center"
                    justifyContent="space-between"
                    columnGap="20px"
                    zIndex="2"
                >
                    <IconButton sx={{ color: "black" }} onClick={handleSearch}>
                        <SearchOutlined />
                    </IconButton>
                    <TextField
                        value={searchQuery}
                        onChange={handleSearchInputChange}
                        placeholder="Search"
                        variant="outlined"
                        sx={{
                            height: "25px",
                            "& input": {
                                paddingTop: "6px",
                                paddingBottom: "6px",
                            },
                        }}
                    />
                    <IconButton sx={{ color: "black" }} onClick={handleNotificationClick}>
                        <Badge
                            color="secondary"
                            variant="dot"
                            invisible={!showNotification}
                        >
                            <NotificationsOutlined />
                        </Badge>
                    </IconButton>
                    <IconButton
                        sx={{
                            color: "green",
                            animation: isShaking ? 'shake 0.5s infinite' : 'none'
                        }}
                        onClick={handleWhatsAppClick}
                    >
                        <WhatsAppIcon sx={{ fontSize: 28 }} />
                    </IconButton>

                    <Snackbar
                        open={showNotification}
                        autoHideDuration={4000}
                        onClose={handleNotificationClose}
                        message="No notifications!"
                        action={
                            <IconButton
                                size="small"
                                color="inherit"
                                onClick={handleNotificationClose}
                            >
                                <CloseOutlined fontSize="small" />
                            </IconButton>
                        }
                    />
                    
                    <IconButton
                        sx={{ color: "black" }}
                        onClick={handleShoppingBagClick}
                    >
                        <ShoppingBagOutlined />
                    </IconButton>
                       
                    <Box
                        sx={{
                            display: "flex",
                            alignItems: "center",
                            position: "relative",
                        }}
                    >
                        <IconButton
                            sx={{ color: "black" }}
                            onClick={handleMenuClick}
                        >
                            <MenuOutlined />
                        </IconButton>
                        {showHomeText && (
                            <Typography
                                variant="body2"
                                sx={{
                                    fontSize: 12,
                                    cursor: "pointer",
                                    position: "absolute",
                                    top: "100%",
                                    left: "50%",
                                    transform: "translateX(-50%)",
                                    fontWeight: "bold",
                                    textTransform: "uppercase",
                                    padding: "2px 8px",
                                    border: "1px solid #000",
                                    borderRadius: "4px",
                                    backgroundColor: "#fff",
                                    zIndex: 1,
                                }}
                                onClick={handleHomeClick}
                            >
                                Home
                            </Typography>
                        )}
                    </Box>
                </Box>
            </Box>
            {searchNotFound && (
                <Typography
                    variant="body2"
                    sx={{
                        fontSize: 14,
                        marginTop: 2,
                        fontWeight: "bold",
                    }}
                >
                    Not found
                </Typography>
            )}

{showShoppingBagPanel && (
  <Box
    position="fixed"
    top={isMobile ? '56px' : '60px'}
    right={isMobile ? '0' : '200px'}
    backgroundColor="rgba(255, 255, 255, 0.9)"
    boxShadow="0px 0px 8px rgba(0, 0, 0, 0.9)"
    borderRadius="4px"
    padding="10px"
    width={isMobile ? '100%' : '280px'}
    height="150px"
    zIndex="1"
  >
    <IconButton
      size="small"
      color="inherit"
      onClick={handleClosePanel}
      sx={{
        position: "absolute",
        top: "5px",
        right: "5px",
      }}
    >
      <CloseOutlined fontSize="small" />
    </IconButton>
    <Typography variant="body2" textAlign="center"><b>Our extra services</b></Typography>

    <Box display="flex" justifyContent="space-around" flexWrap="wrap" mt={2}>
      <Tooltip title="Turnitin Reports" placement="bottom">
        <IconButton size="small" sx={{ color: "#e53414" }}>
          <TurnedIn />
        </IconButton>
      </Tooltip>
      <Tooltip title="Grammer Check" placement="bottom">
        <IconButton size="small" sx={{ color: "#77a9e7" }}>
          <Spellcheck />
        </IconButton>
      </Tooltip>
      <Tooltip title="Translation" placement="bottom">
        <IconButton size="small" sx={{ color: "#16a085" }}>
          <GTranslate />
        </IconButton>
      </Tooltip>
      <Tooltip title="Research materials & Books" placement="bottom">
        <IconButton size="small" sx={{ color: "#2196f3" }}>
          <MenuBook />
        </IconButton>
      </Tooltip>
      <Tooltip title="Unlocks (Chegg & Coursehero)" placement="bottom">
        <IconButton size="small" sx={{ color: "#f9a825" }}>
          <School />
        </IconButton>
      </Tooltip>
      <Tooltip title="Course Notes" placement="bottom">
        <IconButton size="small" sx={{ color: "#9c27b0" }}>
          <LibraryBooks />
        </IconButton>
      </Tooltip>
    </Box>
    <Typography variant="body2" textAlign="center"> Your services are one click away</Typography>
    <Box display="flex" justifyContent="center" alignItems="center" gap={1}>
  <IconButton
    sx={{
      color: "green",
    }}
    onClick={handleWhatsAppClick}
  >
    <WhatsAppIcon sx={{ fontSize: 28 }} />   
  </IconButton>
  <IconButton size="small" sx={{ color: "#C41E3A" }} onClick={handleEmailClick}>
  <EmailIcon sx={{ fontSize: 28 }} />
</IconButton>
</Box>
  </Box>
)}      
        </Box>
    );
};

export default Navbar;
